$global-cta-bg-color: #3F3F3F;
.global-cta {
	background-color: $global-cta-bg-color;
	padding: 5rem 0;
	color: white;
		@include breakpoint(medium) {
			padding: 3.5rem 0;
	 }

	.global-cta-heading p {
		font-size: 2.5rem;
		font-family: $heading;
		margin: 0 0 2rem 0;
			@include breakpoint(medium) {
				font-size: 2rem;
				line-height: 2.4rem;
				margin: 0 0 1rem 0;
		 }
		em {
			color: $brand-primary;
			font-style: italic;
		}
	}
}